import Router from 'vue-router'
import Home from './views/Home.vue'

// about
import About from './views/about/About.vue'
import Contact from './views/about/Contact.vue'
import Locations from './views/about/Locations.vue'

// contractors
import Contractors from './views/contractors/Contractors.vue'
import ContractorsContact from './views/contractors/ContractorsContact.vue'
import ContractorsOrder from './views/contractors/ContractorsOrder.vue'
import ContractorsShow from './views/contractors/ContractorsShow.vue'
import ContractorsSpecials from './views/contractors/ContractorsSpecials.vue'

// designers
import Designers from './views/designers/Designers.vue'
import DesignersContact from './views/designers/DesignersContact.vue'
import DesignersWorkshops from './views/designers/DesignersWorkshops.vue'

// homeowners
