<template>
  <div id="app">
    <Navbar></Navbar>
		<transition 
			name="fade" 
			mode="out-in"
		>
			<router-view></router-view>
		</transition>
    <Footer></Footer>
  </div>
</template>

<style scoped>
.fade-enter-active,
.fade-leave-active {
  transition-duration: 0.3s;
  transition-property: opacity;
	transition-timing-function: ease;
	overflow: hidden;
}

.fade-enter,
.fade-leave-active {
  opacity: 0
}
</style>
<script>
import Navbar from '@/components/nav/Navbar.vue'
import Footer from '@/components/Footer.vue'

export default {
  components: {
    Navbar,
    Footer
  },
  watch: {
    '$route': {
      handler: (to) => {
        if (to.meta.title) {
          document.title = 'Abbott Paint | ' + to.meta.title
        } else {
					document.title = 'Abbott Paint'
				}
      },
      immediate: true
    }
	}
}
</script>
